import shopCate from '@/components/shopCate';
import DialogLoading from '@/views/importList/DialogLoading';
export default {
  props: ["dialogPush", "SelectedPushStoresNum"],
  data: function data() {
    return {
      LoadingVisible: false,
      loadingNum: 0,
      timeOut: 0
    };
  },
  components: {
    shopCate: shopCate,
    DialogLoading: DialogLoading
  },
  methods: {
    pushToShopify: function pushToShopify() {
      var _this = this;

      if (!this.SelectedPushStoresNum) {
        this.$elementMessage("Please select the stores", "error");
        return;
      }

      this.LoadingVisible = true;
      var pushList = [];
      this.dialogPush.shopList.forEach(function (shops) {
        shops.data.forEach(function (shop) {
          _this.dialogPush.items.forEach(function (item) {
            item.pushRe = [];

            if (shop.checked && shop.platform == 1) {
              //shop.selectCollection &&
              //
              pushList.push({
                id: item.id,
                accountId: shop.id,
                collectionId: shop.selectCollection,
                published: _this.dialogPush.published
              }); // console.log(pushList)
            } else if (shop.checked && shop.platform != 1) {
              pushList.push({
                id: item.id,
                accountId: shop.id,
                published: _this.dialogPush.published
              });
            }
          });
        });
      });
      this.timeOut = setInterval(function () {
        if (_this.loadingNum > 94) {
          _this.loadingNum = 94;
        }

        _this.loadingNum += 5;
      }, 100);
      this.$apiCall("api.Product.addImportTask", {
        products: pushList
      }, function (r) {
        if (r.ErrorCode == 9999) {
          _this.loadingNum = 100;
          clearInterval(_this.timeOut);
          _this.dialogPush.isAction = false;
          _this.LoadingVisible = false;
          _this.dialogPush.isShow = false;
          _this.loadingNum = 0;

          _this.$elementMessage("Push completed", "success");

          _this.$parent.pushSuccess();
        } else {
          _this.$elementMessage(r.Message, "error");
        }
      });
    },
    delPushProduct: function delPushProduct(i) {
      //
      this.dialogPush.items.splice(i, 1);
      if (!this.dialogPush.items.length) this.dialogPush.isShow = false;
    },
    selectShopGetCollections: function selectShopGetCollections(shop) {
      var _this2 = this;

      // collections
      if (shop.collections.length) {
        //
        return;
      }

      if (shop.platform != 1) {
        //  Shopify 
        return;
      }

      shop.loading = true;
      this.$apiCall("api.Product.getCollections", {
        id: shop.id
      }, function (r) {
        shop.loading = false;

        if (r.ErrorCode == 9999) {
          // if (!Object.keys(r.Data.Results).length) {
          //   shop.disabled = true;
          //   shop.checked = false;
          //   return;
          // }
          shop.collections = [];
          Object.keys(r.Data.Results).forEach(function (k) {
            shop.collections.push({
              id: k,
              name: r.Data.Results[k]
            });
          });

          if (shop.collections.length == 1) {
            shop.selectCollection = shop.collections[0].id;
          }
        } else {
          _this2.$elementMessage(r.Message, "error");
        }
      });
    },
    closePush: function closePush() {
      this.LoadingVisible = false;
      this.dialogPush.isShow = false;
    },
    closeDialog: function closeDialog() {}
  }
};