          //your price
          var yourPrice = '---';

          if (e.stocks && e.stocks.length) {
            var min = 0,
                max = 0;
            e.stocks.some(function (e) {
              if (e.price) {
                min = max = Number(e.price);
                return true;
              }
            });
            e.stocks.forEach(function (e) {
              if (e.price && min > Number(e.price)) {
                min = Number(e.price);
              }

              if (e.price && max < Number(e.price)) {
                max = Number(e.price);
              }
            });

            if (min == max) {
              yourPrice = min.toFixed(2);
            } else {
              yourPrice = "".concat(min, " - ").concat(max);
            }
          }

          return "".concat(_this17.currency, "  ").concat(yourPrice);
        };

        if (r.ErrorCode == 9999) {
          _this17.items = [];

          if (_this17.$route.name == 'WaitingImport' || _this17.fromPage == 'home') {
            //tabs
            r.Data.Results.forEach(function (e, index) {
