            r.Data.Results.forEach(function (e) {
              e.checked = false;
              e.mainImg = e.product.mainImg;
              e.name = e.product.name;
              e.originUrl = e.product.originUrl;
              e.yourPrice = yourPrice(e);

              _this17.items.push(e);
            });
          }

          _this17.total = Number(r.Data.Pagination.totalCount);
          _this17.totalPage = Number(r.Data.Pagination.totalPage);
          if (!s) _this17.$root.$children[0].$refs.mainScroll.wrap.scrollTop = 0;
        }
      });
    },
