    groupBy: function groupBy(arr) {
      var map = {},
          dest = [];

      for (var i = 0; i < arr.length; i++) {
        var ai = arr[i];

        if (!map[ai.platform]) {
          //
          dest.push({
            platform: ai.platform,
            //
            data: [ai]
          });
          map[ai.platform] = ai; //
        } else {
          for (var j = 0; j < dest.length; j++) {
            var dj = dest[j];

            if (dj.platform == ai.platform) {
              //
              dj.data.push(ai);
              break;
            }
          }
        }
      }

      return dest;
    },
