export default {
  props: ['dialogPushSetting'],
  methods: {
    delRange: function delRange(i) {
      //range
      this.dialogPushSetting.ranger.splice(i, 1);
    },
    addRangeItem: function addRangeItem() {
      //range
      this.dialogPushSetting.ranger.unshift({
        from: 0,
        to: 0.01,
        shippingCost: 0,
        price: 1,
        priceType: "1",
        comparePrice: 1,
        comparePriceType: "2"
      });
    },
    savePushSetting: function savePushSetting(s) {
      this.$emit('savePushSetting', s);
    },
    applyPriceSetting: function applyPriceSetting() {
      this.savePushSetting(true);
    }
  }
};