export default {
  props: ['dialogSelectImg', 'dialogEditProduct'],
  methods: {
    selectImg: function selectImg(img) {
      //
      this.dialogSelectImg.selectImg = img;
    },
    confirmSelectImg: function confirmSelectImg() {
      //
      if (this.dialogSelectImg.item) {
        this.$set(this.dialogSelectImg.item, "propertyImage", this.dialogSelectImg.selectImg);
      } else {
        this.$set(this.dialogEditProduct, "mainImg", this.dialogSelectImg.selectImg);
      }

      this.dialogSelectImg.isShow = false;
    }
  }
};